import { InputProps } from 'core/src/Input';

/** Допустимые для валидации форматы даты */
export enum DateFormatEnum {
  DayMonth = 'dayMonth',
  MonthDay = 'monthDay',
  DayMonthYear = 'dayMonthYear',
  MonthDayYear = 'monthDayYear'
}

/** Допустимые для валидации форматы времени */
export enum TimeFormatEnum {
  HoursMinutes = 'hoursMinutes',
  MinutesSeconds = 'minutesSeconds',
  HoursMinutesSeconds = 'hoursMinutesSeconds'
}

export type DateFormat = `${ DateFormatEnum }`;

export type TimeFormat = `${ TimeFormatEnum }`;

/** Все доступные для валидации форматы */
export type ValidateFormat = DateFormat | TimeFormat;

export interface MaskedInputProps extends InputProps {
  /** Маска-плейсхолдер, которая используется в качестве плейсхолдера для инпута и маски для ввода */
  placeholderMask: string;

  /** Формат маски для встроенной валидации */
  validationFormat?: ValidateFormat;
}

export interface KeyDownInfoState {
  /** Текущая позиция курсора */
  currentPosition: number;

  /** Последняя нажатая клавиша */
  pressedKey: string;
}
